import { httpGet, httpPost, basePath } from "./../request";
const ClassPractise = `${basePath}/I_ClassPractiseService/`;
export default {
    /*** ****/
    getList: data => {
        return httpGet(
            `${ClassPractise}GetPLStatsByCIID`,
            data
        );
    },
    // 
    getDataByIndex: data => {
        return httpGet(
            `${ClassPractise}GetPSAndPA`,
            data
        );
    },
    // 
    getDataById: data => {
        return httpGet(
            `${ClassPractise}GetPSData`,
            data
        );
    },
    /*** ****/
    getPracticeMental: data => {
        return httpGet(
            `${ClassPractise}GetPLStatsByPLID`,
            data
        );
    },
    getStatistic: data => {
        return httpGet(
            `${ClassPractise}GetPLStatsByCIID`,
            data
        );
    },
    getData: data => {
        return httpGet(
            `${ClassPractise}GetPSAndPA`,
            data
        );
    },

    // 
    postDataAnswer: data => {
        return httpPost(
            `${ClassPractise}SubmitAnswer`,
            data
        );
    },
    // 
    postDataPractise: data => {
        return httpPost(
            `${ClassPractise}SubmitPractise`,
            data
        );
    },
    // 
    postDataSurvey: data => {
        return httpPost(
            `${ClassPractise}SubmitSurvey`,
            data
        );
    }
};


