import { httpGet, httpPost, basePath } from "./../request";

const Group = `${basePath}/I_GroupService/`;
export default {
  // 
  getRankList: data => {
    return httpGet(`${Group}GetGroupRankingByMember`, data);
  },
  getDiscussRankList: data => {
    return httpGet(`${Group}GetGroupMemberRanking`, data);
  },
  // 
  getStudentGroup: data => {
    return httpGet(`${Group}GetStudentGroup`, data);
  },
  // 
  postJoinGroup: data => {
    return httpPost(`${Group}JoinGroup`, data);
  },
  // 
  postDataSave: data => {
    return httpPost(`${Group}UpdateGroupName`, data);
  },
  // 
  postQuitGroup: data => {
    return httpPost(`${Group}QuitGroup`, data);
  },

};



